<template>
  <header :class="['scondTitle',{'fixed':fixed}]" :style="{'marginTop':statusBarHeight + 'px'}">
    <img class="leftImg" :src="center?'./static/img/ic_public_close_blue.png':'./static/img/icon_back_blue.png'" @click.stop="cancel">
    <span :style="{'text-align': center?'center':'left'}">{{title}}</span>
    <img class="rightImg" v-if="center" :src="'./static/img/ic_public_sure_blue.png'" @click.stop="submit">
  </header>
</template>
<script>
export default {
  name: 'SecondTitle',
  props: ['title', 'center', 'fixed'],
  data () {
    return {
      statusBarHeight: 20
    }
  },
  created () {
    let _self = this
    window.SecondTitleBarHeightResult = function (res) {
      let data = JSON.parse(res)
      _self.statusBarHeight = data.statusBarHeight
    }
    if (!window.setTitleVisiableResult) window.setTitleVisiableResult = _ => {}
    window.hilink.setTitleVisible(false, 'setTitleVisiableResult')
    if (window.hilink.getStatusBarHeight) {
      window.hilink.getStatusBarHeight('SecondTitleBarHeightResult')
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
    },
    submit () {
      this.$emit('submit')
    }
  }
}
</script>
<style lang="css" scoped>
 header{
    width: 100%;
    height: 4.8rem;
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: 2px solid rgba(0, 0, 0, .1);
  }
  header.fixed {
    position: fixed;
    top: 0;
    left: 0;
  }
  header img{
    width: 1.8rem;
    height:1.8rem;
  }
  header .leftImg {
    margin-left: 1.6rem;
  }
  header .rightImg {
    margin-right: 1.6rem;
  }
  header span{
    flex: 1;
    font-size: 1.6rem;
    margin: 0 1.6rem;
  }
</style>



